import React from "react";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import styled from "styled-components";

export default function CompanyData({ data }) {

    function createData (name, data) {
        return { name, data }
    }
    const rows = [
        createData('50 Day Moving Average', data['50DayMovingAverage']),
        createData('52 Week High', data['52WeekHigh']),
        createData('52 Week Low', data['52WeekLow']),
        createData('200 Day Moving Average', data['200DayMovingAverage']),
        createData('Analyst Target Price', data['AnalystTargetPrice']),
        createData('Book Value', data['BookValue']),
        createData('Diluted EPSTTM', data['DilutedEPSTTM']),
        createData('Dividend Date', data['DividendDate']),
        createData('Dividend Per Share', data['DividendPerShare']),
        createData('Dividend Yield', data['DividendYield']),
        createData('EV To EBITDA', data['EVToEBITDA']),
        createData('PEG Ratio', data['PEGRatio']),
        createData('PE Ratio', data['PERatio'])
    ]

    const StyledP = styled.p`
        width: 650px;
        background-color: #F6EABE;
        border-radius: 2rem;
        padding: 20px;
        color: #3E8E7E; 
        margin-bottom: 20px;
    `

    return (
        <>
        <h1>{data.Name} {data.Symbol}</h1>
        <StyledP>{data.Description}</StyledP>
        <TableContainer sx={{maxWidth: 700}} component={Paper}>
                <Table sx={{minWidth: 650}} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>항목</TableCell>
                            <TableCell>데이터</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow 
                                key={row.date}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell align="left">{row.data}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}