import react from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default function TimeTable ({ data }) {
    function createData (date, open, high, low, close, adjustedClose) {
        return { date, open, high, low, close, adjustedClose }
    }

    const timeSeries = data

    let rows = timeSeries.map(item => {
            return createData(item[0], item[1]['1. open'], item[1]['2. high'], item[1]['3. low'], item[1]['4. close'], item[1]['5. adjusted close'])
        }) 

    return (
        <>
            <TableContainer sx={{maxWidth: 700}} component={Paper}>
                <Table sx={{minWidth: 650}} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>날짜</TableCell>
                            <TableCell>Open</TableCell>
                            <TableCell>High</TableCell>
                            <TableCell>low</TableCell>
                            <TableCell>Close</TableCell>
                            <TableCell>Adjusted Close</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow 
                                key={row.date}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell component="th" scope="row">
                                    {row.date}
                                </TableCell>
                                <TableCell align="left">{row.open}</TableCell>
                                <TableCell align="left">{row.high}</TableCell>
                                <TableCell align="left">{row.low}</TableCell>
                                <TableCell align="left">{row.close}</TableCell>
                                <TableCell align="left">{row.adjustedClose}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
    }