import React, { useState, useEffect } from "react";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import axios from "axios";
import TimeTable from "./timedata";

export default function Historical() {
    
    useEffect(() => {
        document
            .getElementById('historySubmit')
            .addEventListener('keydown', (e) => {
                if (e.code === 'Enter' || e.code === 'NumpadEnter') {
                    e.preventDefault()
                    document.getElementById('historySubmit').click()
                }
            });
    }, [])

    const [symbol, setSymbol] = useState("");
    const [data, setData] = useState([]);

    const api_key = 'OT9Y8WA3SW2KGZYP'
    const url = `https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${symbol}&apikey=${api_key}`
    
    const handleChange = (e) => {
        setSymbol(e.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        axios.get(url)
        .then(r => setData(() => {
            const newData = Object.entries(Object.values(r.data)[1])
            return newData
        }))
    }
    
    return (
        <>
            <form onSubmit={handleSubmit}>
                <TextField 
                    size="small" 
                    placeholder="티커" 
                    sx={{mt: 2}}
                    onChange={handleChange} 
                />
                <Button 
                    id="historySubmit"
                    variant="outlined" 
                    type="submit"
                    sx={{mt: 2, ml: 2}}
                >검색</Button>
            </form>
            <div>
                {data ? <TimeTable data={data} /> : ""}
            </div>
        </>
    )
}