import react, { useState, useEffect } from "react"
import axios from "axios";
import DataTable from './datatable'
import { Autocomplete } from "@mui/material";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import styled from "styled-components";


export default function Search() {
    useEffect(() => {
        document
            .getElementById('querySubmit')
            .addEventListener('keydown', (e) => {
                if (e.code === 'Enter' || e.code === 'NumpadEnter') {
                    e.preventDefault()
                    document.getElementById('querySubmit').click()
                }
            });
    }, [])

    useEffect(() => {
        document
            .getElementById('tickerSubmit')
            .addEventListener('keydown', (e) => {
                if (e.code === 'Enter' || e.code === 'NumpadEnter') {
                    e.preventDefault()
                    document.getElementById('tickerSubmit').click()
                }
            });
    }, [])

    const [query, setQuery] = useState("");
    const [ticker, setTicker] = useState("");
    const [queryResult, setQueryResult] = useState([]);
    const [quoteResult, setQuoteResult] = useState({});

    const api_key = "R5LFZaoEoF6pTDkjoVVm07lLndSG2qkO1P1S4rUX";
    const url = "https:/yfapi.net";

    // ticker options
    const options = queryResult.map(item => item.symbol);

    // handle query for Autocomplete
    const handleAutoSubmit = (e) => {
        e.preventDefault()
        const quote = url.concat("/v6/finance/autocomplete")
        const axios = require("axios").default;

        const options = {
        method: 'GET',
        url: quote,
        headers: {
            'x-api-key': api_key
        },
        params: {'query': query, 'lang': 'en'},
        };

        axios.request(options).then(function (response) {
            setQueryResult(() => {
                const newResult = response.data.ResultSet.Result
                return newResult
            });
            console.log(queryResult)
        }).catch(function (error) {
            console.error(error);
        });
        }
    
    // handle ticker input
    const handleTickerInput = (event, value) => {
        setTicker(value)
    }

    // handle Quote Search
    const handleQuoteSearch = (e) => {
        e.preventDefault()
        const quote = url.concat("/v6/finance/quote")
        const axios = require("axios").default;

        const options = {
        method: 'GET',
        url: quote,
        headers: {
            'x-api-key': api_key
        },
        params: {'symbols': ticker, 'lang': 'en'},
        };

        axios.request(options).then(function (response) {
            setQuoteResult(() => {
                const newResult = response.data.quoteResponse.result[0]
                return newResult
            });
            console.log(quoteResult)
        }).catch(function (error) {
            console.error(error);
        });
    }

    const StyledAutocomplete = styled(Autocomplete)`
        display: inline-block;
    `

    return (
        <>
            <form onSubmit={handleAutoSubmit} id="form-autocomplete" >
                <TextField 
                    id="outlined-basic" 
                    constiant="outlined" 
                    size="small"
                    sx={{
                        mt: 2
                    }}
                    placeholder="종목명 검색"
                    onChange={e => setQuery(e.target.value)} 
                />
                <Button
                    id="querySubmit"
                    variant="outlined" 
                    type="submit"
                    sx={{
                        display: 'inline',
                        ml: 1,
                        mt: 2
                    }}
                >검색</Button>
                
            </form>
            <form onSubmit={handleQuoteSearch} id="ticker-search">
                <StyledAutocomplete
                        disablePortal
                        id="combo-box-demo"
                        options={options}
                        sx={{ 
                            width: 200,
                        }}
                        size="small"
                        renderInput={(params) => <TextField {...params} label="티커" />}
                        onChange={handleTickerInput}
                    />
                <Button 
                    id="tickerSubmit"
                    variant="outlined" 
                    type="submit"
                    sx={{mt: 2, ml: 1}}
                >검색</Button>
            </form>
        <div>
            { quoteResult ? <DataTable quoteResult={quoteResult} /> : "" }
        </div>
        </>
    )
}