import react from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default function DataTable ({ quoteResult }) {
    function createData (name, data) {
        return { name, data }
    }

    const rows = [
        createData('Ask', quoteResult.ask),
        createData('Average Analyst Rating', quoteResult.averageAnalystRating),
        createData('EPS Current Year', quoteResult.epsCurrentYear),
        createData('50 Day Average', quoteResult.fiftyDayAverage),
        createData('50 Day Average Change', quoteResult.fiftyDayAverageChange),
        createData('52 Week High', quoteResult.fiftyTwoWeekHigh),
        createData('52 Week Low', quoteResult.fiftyTwoWeekLow),
        createData('52 Week Range', quoteResult.fiftyTwoWeekRange)
    ]

    return (
        <>
        <h1> {quoteResult.displayName} </h1>
            <TableContainer component={Paper} sx={{maxWidth: 570}}>
                <Table sx={{minWidth: 650}} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>항목</TableCell>
                            <TableCell>데이터</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow 
                                key={row.name}
                                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                            >
                                <TableCell 
                                    component="th" 
                                    scope="row"
                                    sx={{width: 250}}
                                >
                                    {row.name}
                                </TableCell>
                                <TableCell align="left">{row.data}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}